import React, { PropTypes,Component } from 'react'
import { Form, Input, InputNumber, Radio, Modal} from 'antd'
import { Upload, message, Button, Icon,Tooltip} from 'antd';
import { Select } from 'antd';
import PictureWall from '../upload/PictureWall';
import {downdomain} from '../../utils/config';
import styles from './Myproducts.css'
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;

const formItemLayout = {
  labelCol: {
    span: 6
  },
  wrapperCol: {
    span: 14
  }
}

class modall extends Component{
  constructor(props){
    super(props);
    this.state={
      verifyModal: props.item.custom,
    }
  }
  handleOk=()=>{
    this.props.form.validateFields((errors,values)=>{
      if(errors){
        return
      }
      this.props.onOk(values)
    })
  }
  handleBlur=()=>{
    this.props.form.validateFields((errors,values)=>{
      if(values.maxPriceY<values.minPriceY){
        this.props.onBlurs()
      }
    })
  }

  switchModalMethod = (e)=>{
    this.setState({verifyModal : e.target.value == '0' ? false : true});
  }

  onKeyDownone =(e)=>{
  if(e.keyCode >=48 && e.keyCode <= 57){
  }else if(e.keyCode==8){
    if(e&& e.stopPropagation){
      code= e.which;
    }else{
      code= window.event.keyCode;
    }
  }else{
    if(e.keyCode == 190){
    }else{
      e.preventDefault();
    }
  }
};
  onKeyDowntwo =(e)=>{
  console.log(e.keyCode);
  if(e.keyCode >=48 && e.keyCode <= 57){

  }else if(e.keyCode==8){
    if(e&& e.stopPropagation){
      code= e.which;
    }else{
      code= window.event.keyCode;
    }
  }else{
    e.preventDefault();
  }
};
  onListChange=(fileList)=>{
    this.fileList = fileList;
    const fieldsValue = this.props.form.getFieldsValue();
    const productThumb = fileList.map((item)=>item.response.key);
    const newFieldsValue = {...fieldsValue, 'productImg': productThumb.length > 0 ? downdomain + productThumb : ''};
    this.props.form.setFieldsValue(newFieldsValue);
    this.props.form.validateFields();
  };


  render(){
    const {
      form: {
        getFieldDecorator,
        validateFields,
        getFieldsValue
        },
      item={},
      productTypes,
      fausetipVisible
      }=this.props;

    const fileList = item.productThumb ? [{
      uid: -1,
      status: 'done',
      name: item.productThumb,
      url: item.productThumb
    }] : [];


    const {orderType, serviceType,unitList} = productTypes;

    const Test  = () =>
      <Select  style={{ width: 120 }}>
        {orderType.map(({value, text},index) => {
          return <Option key={value} value={value}>{text}</Option>
        })}
      </Select>


    return(
      <div className={styles.form+'shadow'}>
        <form>
          <FormItem label='' hasFeedback {...formItemLayout}>
            {getFieldDecorator('productName', {
              initialValue: item.productName,
              rules: [
                {
                  required: true,
                  message: ''
                }
              ]
            })(<Input />)}
          </FormItem>
          <FormItem label='' hasFeedback {...formItemLayout}>
            {getFieldDecorator('productBrief', {
              initialValue: item.productBrief,
              rules: [
                {
                  required: true,
                  message: ''
                }
              ]
            })(<Input type="textarea" rows={10}/>)}
          </FormItem>
          <FormItem label='' hasFeedback {...formItemLayout}>
            {getFieldDecorator('productTypeId', {
              initialValue: item.productTypeId.toString(),
              rules: [
                {
                  required: true,
                  message: ''
                }
              ]
            })(
              <Select  style={{ width: 120 }}>
                {orderType.map(({value, text},index) => {
                  return <Option key={value} value={value}>{text}</Option>
                })}
              </Select>
            )}
          </FormItem>
          <FormItem label='' hasFeedback {...formItemLayout}>
            {getFieldDecorator('serviceType', {
              initialValue: item.serviceType.toString(),
              rules: [
                {
                  required: true,
                  message: ''
                }
              ]
            })(
              <Select  style={{ width: 120 }} >
                {serviceType.map(({value, text},index) => {
                  return <Option key={index} value={value}>{text}</Option>
                })}
              </Select>
            )}
          </FormItem>
          <FormItem label='' hasFeedback {...formItemLayout}>
            {getFieldDecorator('unit', {
              initialValue: item.unit.toString(),
              rules: [
                {
                  required: true,
                  message: ''
                }
              ]
            })(
              <Select  style={{ width: 120 }} >
                {unitList.map(({value, text},index) => {
                  return <Option key={index} value={value}>{text}</Option>
                })}
              </Select>
            )}
          </FormItem>

          {(!this.state || !this.state.verifyModal)?
          <FormItem label='' hasFeedback {...formItemLayout} >
            {getFieldDecorator('shopPriceY',{
              initialValue: item.shopPriceY || 0,
              rules: [
                {
                  required: true,
                  message: ''
                }
              ]
            })(<InputNumber className={styles.minprice} min="0" onKeyDown={this.onKeyDownone}/>)}
          </FormItem>
            : <div>
             <FormItem label='' hasFeedback {...formItemLayout} >
              {getFieldDecorator('minPriceY',{
                initialValue:item.minPriceY || 0,
                rules: [
                  {
                    required: true,
                    message:''
                  }
                ]
              })(<InputNumber className={styles.minprice} min="0" onKeyDown={this.onKeyDownone}/>)}
             </FormItem>
             <FormItem label='' hasFeedback {...formItemLayout}>
              {getFieldDecorator('maxPriceY',{
                initialValue:item.maxPriceY || 0,
                rules: [
                  {
                    required: true,
                    message:''
                  }
                ]
              })(<InputNumber className={styles.minprice} min="0" onKeyDown={this.onKeyDownone} onBlur={this.handleBlur}/>)}
            </FormItem>
          </div>
          }
          <FormItem label=''  hasFeedback {...formItemLayout}>
            {getFieldDecorator('custom',{
              initialValue: item.custom,
              rules: [
                {
                  required: true,
                  message:''
                }
              ]
            })(<RadioGroup onChange={this.switchModalMethod}>
                <Radio value={0}></Radio>
                <Radio value={1}></Radio>
              </RadioGroup>
            )}
          </FormItem>
          <FormItem label='' hasFeedback {...formItemLayout}>
            {getFieldDecorator('productImg', {
              initialValue: item.productImg,
              rules: [
                {
                  required: true,
                  message: ''
                },
              ]
            })(<Input type="hidden" />)}
            <PictureWall maxFile={1} fileList={fileList} onListChange={this.onListChange}/>
          </FormItem>
          <a onClick={this.handleOk} className={styles.surebutton}></a>
        </form>
        {fausetipVisible?<p className={styles.falsetip_price}>,!</p>:''}
      </div>
    )
  }
}

modall.propTypes = {
  visible: PropTypes.any,
  form: PropTypes.object,
  item: PropTypes.object,
  onOk: PropTypes.func,
  onCancel: PropTypes.func
}

export default Form.create()(modall)



