import React from 'react';
import { connect } from 'dva';
import styles from './Myproducts.css';
import { routerRedux } from 'dva/router';
import {Modal} from 'antd';
import MyproductsList from '../components/Myproducts/Myproducts';
import Search from '../components/Myproducts/search';
import Modall from '../components/Myproducts/modal'

function Myproducts({dispatch, location, myproducts,modall}){

  const {loading, list, pagination, currentItem, modalType ,modalVisible,productTypes, fausetipVisible} = myproducts
  const { field, keyword } = location.query



  const ModalProps = {
    ...modall,
    fausetipVisible,
    item: modalType === 'create' ? {} : currentItem,
    type: modalType,
    visible: modalVisible,
    productTypes: productTypes,
    onBlurs(){
      dispatch({
        type: 'myproducts/fausetip'
      })
    },
    onOk (data) {
      if(data.minPriceY<=maxPriceY){
        dispatch({
          type: `myproducts/${modalType}`,
          payload: data
        })
      }else {
        return
      }
    },
    onCancel () {
      dispatch({
        type: 'myproducts/hideModal'
      })
    },
  }
  const MyproductsListProps = {
    dataSource: list,
    loading,
    pagination: pagination,
    onPageChange (page) {
      const { query, pathname } = location
      dispatch(routerRedux.push({
        pathname: pathname,
        query: {
          ...query,
          page: page.current,
          pageSize: page.pageSize
        }
      }))
    },
    onDeleteItem (id) {
      dispatch({
        type: 'myproducts/delete',
        payload: id
      })
    },
    onEditItem (item) {
      dispatch({
        type: 'myproducts/showModal',
        payload: {
          modalType: 'update',
          currentItem: item
        }
      })
    }
  }
  const cancelLoginModal =()=> {
    dispatch({
      type: 'myproducts/hideModal'
    })
  }

  const myproductsSearchProps = {
    field,
    keyword,
    onSearch (fieldsValue) {
      fieldsValue.keyword.length ? dispatch(routerRedux.push({
        pathname: '/admin/myproducts',
        query: {
          field: fieldsValue.field,
          keyword: fieldsValue.keyword
        }
      })) : dispatch(routerRedux.push({
        pathname: '/admin/myproducts',
      }))
    },
    onAdd () {
      dispatch({
        type: 'myproducts/showModal',
        payload: {
          modalType: 'create'
        }
      })
    }
  }


  const ModalGen = () =>
    <Modal title="" visible={modalVisible}  onCancel={cancelLoginModal}>
      <div>
        <Modall {...ModalProps}/>
      </div>
    </Modal>

  return (
    <div className={styles.normal}>
      <Search {...myproductsSearchProps}/>
      <MyproductsList {...MyproductsListProps}/>
      <ModalGen/>
    </div>
  );
}

function mapStateToProps({myproducts }) {
  return {myproducts};
}

export default connect(mapStateToProps)(Myproducts);




